.cart{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;
    margin-bottom: 200px;
}

.cart-items{
    display: flex;
    flex-direction: column;
    margin: 15px;
}


.cartButton{
    display: flex;
    flex-flow: row wrap;
    gap: 1rem;
    justify-content: center;
}

button{
    cursor: pointer;
    width: 5rem;
    margin-top: 10px;
    border-radius: 7px;
    background-color: white;
}

button:hover{
    background-color:gold;
    color: rgba(255, 255, 255, 0.84);;
}